:root {
  --lx-base-size: 100%;
  --lx-gap: 1.125em;
  --lx-radius-01: 0.375rem;
  --lx-radius-02: 0.75rem;
  --lx-radius-03: 1.125rem;
  --lx-error: hsl(354, 100%, 59%);
  --lx-info: hsl(205, 100%, 45%);
  --lx-success: hsl(110, 100%, 35%);
  --lx-warning: hsl(29, 100%, 55%);
  --lx-neutral-01: hsl(0, 0%, 96%);
  --lx-neutral-02: hsl(0, 0%, 72%);
  --lx-neutral-03: hsl(0, 0%, 48%);
  --lx-neutral-04: hsl(0, 0%, 36%);
  --lx-neutral-05: hsl(0, 0%, 24%);
  --lx-neutral-06: hsl(0, 0%, 12%);
}

@supports (font-size: clamp(1rem, 1vw, 1rem)) {
  :root {
    --lx-text-01: clamp(0.75rem, 0.63vw + 0.63rem, 1.13rem);
    --lx-text-02: clamp(0.9rem, 0.84vw + 0.73rem, 1.41rem);
    --lx-text-03: clamp(1.08rem, 1.13vw + 0.85rem, 1.76rem);
    --lx-text-04: clamp(1.3rem, 1.5vw + 1rem, 2.2rem);
    --lx-text-05: clamp(1.56rem, 1.99vw + 1.16rem, 2.75rem);
    --lx-text-06: clamp(1.87rem, 2.61vw + 1.34rem, 3.43rem);
  }
}
@supports not (font-size: clamp(1rem, 1vw, 1rem)) {
  :root {
    --lx-text-01: 0.75rem;
    --lx-text-02: 0.9rem;
    --lx-text-03: 1.08rem;
    --lx-text-04: 1.3rem;
    --lx-text-05: 1.56rem;
    --lx-text-06: 1.87rem;
  }
  @media screen and (min-width: 80rem) {
    :root {
      --lx-text-01: 1.13rem;
      --lx-text-02: 1.41rem;
      --lx-text-03: 1.76rem;
      --lx-text-04: 2.2rem;
      --lx-text-05: 2.75rem;
      --lx-text-06: 3.43rem;
    }
  }
}
:root {
  --lx-space-01: clamp(0.75rem, 0.625rem + 0.625vw, 1.125rem);
  --lx-space-02: clamp(1.125rem, 0.9375rem + 0.9375vw, 1.6875rem);
  --lx-space-03: clamp(1.5rem, 1.25rem + 1.25vw, 2.25rem);
  --lx-space-04: clamp(2.25rem, 1.875rem + 1.875vw, 3.375rem);
  --lx-space-05: clamp(3rem, 2.5rem + 2.5vw, 4.5rem);
  --lx-space-06: clamp(4.5rem, 3.75rem + 3.75vw, 6.75rem);
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media (prefers-reduced-motion: no-preference) {
  * {
    scroll-behavior: smooth;
  }
}

html,
body {
  margin: 0;
  padding: 0;
  word-break: break-word;
  text-wrap: balance;
  line-height: 1.8;
}

html {
  text-size-adjust: 100%;
  font-size: var(--base-size);
  font-variation-settings: normal;
}

body {
  font-size: var(--lx-text-01);
}

#root,
#__next {
  isolation: isolate;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 700;
}

h1 {
  font-size: var(--lx-text-06);
}

h2 {
  font-size: var(--lx-text-05);
}

h3 {
  font-size: var(--lx-text-04);
}

h4 {
  font-size: var(--lx-text-03);
}

h5,
h6 {
  font-size: var(--lx-text-02);
}

p,
b,
strong,
sub,
sup {
  margin: 0;
}

b,
strong {
  font-weight: 700;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

figure,
article,
img {
  margin: 0;
  padding: 0;
  border-style: none;
}

img {
  max-width: 100%;
  max-height: 100%;
}

hr {
  width: 100%;
  height: 0.125rem;
  margin: 0;
  padding: 0;
  position: relative;
  display: block;
  border: none;
}

.lx-container-50 {
  width: 50%;
  max-width: 60rem;
  min-width: 10rem;
  margin: 0 auto;
}
.lx-container-55 {
  width: 55%;
  max-width: 66rem;
  min-width: 11rem;
  margin: 0 auto;
}
.lx-container-60 {
  width: 60%;
  max-width: 72rem;
  min-width: 12rem;
  margin: 0 auto;
}
.lx-container-65 {
  width: 65%;
  max-width: 78rem;
  min-width: 13rem;
  margin: 0 auto;
}
.lx-container-70 {
  width: 70%;
  max-width: 84rem;
  min-width: 14rem;
  margin: 0 auto;
}
.lx-container-75 {
  width: 75%;
  max-width: 90rem;
  min-width: 15rem;
  margin: 0 auto;
}
.lx-container-80 {
  width: 80%;
  max-width: 96rem;
  min-width: 16rem;
  margin: 0 auto;
}
.lx-container-85 {
  width: 85%;
  max-width: 102rem;
  min-width: 17rem;
  margin: 0 auto;
}
.lx-container-90 {
  width: 90%;
  max-width: 108rem;
  min-width: 18rem;
  margin: 0 auto;
}
.lx-container-95 {
  width: 95%;
  max-width: 114rem;
