  margin: 0 auto;
}
.lx-container-100 {
  width: 100%;
  max-width: 120rem;
  min-width: 20rem;
  margin: 0 auto;
}

.lx-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--lx-gap);
}
.lx-row.is-gapless {
  gap: 0;
}

.lx-col {
  display: block;
  flex-basis: 0;
  flex-grow: 1;
  flex-shrink: 1;
}
.lx-col.is-1 {
  width: 8.3333333333%;
  flex: none;
}
.lx-col.is-2 {
  width: 16.6666666667%;
  flex: none;
}
.lx-col.is-3 {
  width: 25%;
  flex: none;
}
.lx-col.is-4 {
  width: 33.3333333333%;
  flex: none;
}
.lx-col.is-5 {
  width: 41.6666666667%;
  flex: none;
}
.lx-col.is-6 {
  width: 50%;
  flex: none;
}
.lx-col.is-7 {
  width: 58.3333333333%;
  flex: none;
}
.lx-col.is-8 {
  width: 66.6666666667%;
  flex: none;
}
.lx-col.is-9 {
  width: 75%;
  flex: none;
}
.lx-col.is-10 {
  width: 83.3333333333%;
  flex: none;
}
.lx-col.is-11 {
  width: 91.6666666667%;
  flex: none;
}
.lx-col.is-12 {
  width: 100%;
  flex: none;
}

@media screen and (max-width: 80rem) {
  .lx-container-50 {
    max-width: 24rem;
  }
  .lx-container-55 {
    max-width: 26.4rem;
  }
  .lx-container-60 {
    max-width: 28.8rem;
  }
  .lx-container-65 {
    max-width: 31.2rem;
  }
  .lx-container-70 {
    max-width: 33.6rem;
  }
  .lx-container-75 {
    max-width: 36rem;
  }
  .lx-container-80 {
    max-width: 38.4rem;
  }
  .lx-container-85 {
    max-width: 40.8rem;
  }
  .lx-container-90 {
    max-width: 43.2rem;
  }
  .lx-container-95 {
    max-width: 45.6rem;
  }
  .lx-container-100 {
    max-width: 48rem;
  }
  .lx-row {
    flex-direction: column;
  }
  .lx-col {
    width: 100%;
  }
}
.hide,
.hide-on-desk {
  display: none;
  visibility: hidden;
}

.has-dflex {
  display: flex;
}
.has-dflex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.flow-column-wrap {
  flex-direction: column;
  flex-wrap: wrap;
}
.flow-column-nowrap {
  flex-direction: column;
  flex-wrap: nowrap;
}
.flow-row-wrap {
  flex-direction: row;
  flex-wrap: wrap;
}
.flow-row-nowrap {
  flex-direction: row;
  flex-wrap: nowrap;
}

.align-center {
  align-items: center;
}
.align-end {
  align-items: flex-end;
}
.align-start {
  align-items: flex-start;
}
.align-stretch {
  align-items: stretch;
}

.justify-around {
  justify-content: space-around;
}
.justify-between {
  justify-content: space-between;
}
.justify-evenly {
  justify-content: space-evenly;
}
.justify-center {
  justify-content: center;
}
.justify-end {
  justify-content: flex-end;
}
.justify-start {
  justify-content: flex-start;
}

.is-gapless {
  gap: 0;
}

@media screen and (max-width: 80rem) {
  .hide-on-desk {
    display: block;
    visibility: visible;
  }
  .hide-on-mb {
    display: none;
    visibility: hidden;
  }
}